.home {
  margin-left: 18rem; }
  @media screen and (max-width: 950px) {
    .home {
      margin-left: 0; } }

.intro {
  background-image: url(/my_portfolio/static/media/intro.80ca91f6.jpg);
  background-position: left;
  background-size: cover;
  height: 22rem;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.intro h1 {
  color: white;
  text-align: center;
  font-weight: 500;
  font-size: 3rem;
  text-shadow: 2px 4px 5px black;
  padding: 0;
  margin: 0; }

.intro h2 {
  color: white;
  text-align: center;
  font-weight: 500;
  font-size: 2rem;
  text-shadow: 2px 4px 5px black;
  padding: 0;
  margin: 0; }

.paragraphs {
  display: flex;
  flex-direction: column;
  margin-top: -3rem; }

.paragraph {
  margin: 1rem; }

.leftLine {
  background-image: linear-gradient(to left, #3b62b1, transparent);
  height: 1px; }

.rightLine {
  background-image: linear-gradient(to right, #3b62b1, transparent);
  height: 1px; }

.paragraphTitle {
  flex: 1 1;
  font-family: Roboto;
  font-weight: 300;
  font-size: 1.9rem;
  letter-spacing: -0.5px;
  color: #3b62b1;
  mix-blend-mode: normal;
  text-align: center; }

.leftTitle {
  text-align: left; }

.rightTitle {
  text-align: right; }

.paragraphContainer {
  display: flex;
  justify-content: center; }

.paragraphContainer.leftParagraph {
  flex-direction: row;
  flex-wrap: wrap-reverse; }

.paragraphContainer.rightParagraph {
  flex-direction: row-reverse;
  flex-wrap: wrap-reverse; }

.paragraphImage {
  height: 5rem;
  align-self: center; }
  @media screen and (max-width: 950px) {
    .paragraphImage {
      height: 3rem; } }

.sectionsContainer {
  flex: 1 1;
  align-self: center;
  font-family: Roboto;
  font-size: 0.8rem;
  letter-spacing: 0.5px;
  background-color: #fff;
  z-index: 1; }

.sectionContainer {
  display: flex;
  flex-direction: row;
  justify-content: center; }
  @media screen and (max-width: 950px) {
    .sectionContainer {
      flex-wrap: wrap; } }

.sectionContent {
  padding: 1rem;
  padding-top: 0px;
  max-width: 35rem; }
  .sectionContent h3 {
    margin: 0;
    margin-bottom: 0.3rem;
    padding: 0;
    text-align: left;
    font-size: 1.2rem; }
    @media screen and (max-width: 950px) {
      .sectionContent h3 {
        text-align: center;
        margin: 0.3rem; } }

.sectionContent.date {
  min-width: 10rem;
  max-width: 12rem;
  align-self: center;
  text-align: center; }
  @media screen and (max-width: 950px) {
    .sectionContent.date {
      padding-bottom: 0;
      margin-bottom: 0; } }

.sectionContent.description {
  min-width: 25rem;
  font-size: 1rem;
  flex: 1 1;
  text-align: left; }
  @media screen and (max-width: 950px) {
    .sectionContent.description {
      min-width: 100%;
      text-align: center; } }

.more-paragraph {
  text-align: center;
  cursor: pointer;
  color: #3b62b1;
  margin-top: -1rem; }

.more-container {
  visibility: hidden;
  opacity: 0;
  width: 100%;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  z-index: -1;
  transition: all 0.5s; }

.more-container-visible {
  visibility: visible;
  opacity: 1;
  z-index: 1;
  -webkit-transform: translateY(0%);
          transform: translateY(0%); }

.menu {
  position: -webkit-sticky;
  position: sticky;
  background-color: #313131;
  top: 0;
  margin-left: 1px;
  color: white;
  height: 3rem;
  display: flex;
  z-index: 2; }
  @media screen and (max-width: 950px) {
    .menu {
      margin-left: 0px; } }
  .menu ul {
    flex: 1 1;
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style-type: none; }
    .menu ul li {
      margin-left: 1rem;
      margin-right: 1rem; }
      .menu ul li a {
        color: white;
        text-decoration: none; }
    @media screen and (max-width: 950px) {
      .menu ul {
        display: none; } }
  .menu .info-menu {
    display: none;
    cursor: pointer;
    height: 1.5rem;
    align-self: center;
    margin-left: 1rem; }
    @media screen and (max-width: 950px) {
      .menu .info-menu {
        display: inline; } }
  .menu p {
    display: none;
    cursor: pointer;
    margin-left: 0.5rem; }
    @media screen and (max-width: 950px) {
      .menu p {
        display: inline; } }

.open-header {
  margin-left: 0px;
  height: auto;
  opacity: 100; }

header {
  width: 18rem;
  height: 100vh;
  display: flex;
  position: fixed;
  border-right-style: solid;
  border-right-width: 0.5px;
  border-right-color: white;
  background-color: #3b62b1; }
  @media screen and (max-width: 950px) {
    header {
      position: fixed;
      top: 0;
      opacity: 0;
      width: 100vw;
      max-height: 100vh;
      height: 0;
      z-index: 10;
      margin-left: -100vw;
      transition: all 0.4s;
      overflow-y: scroll; } }
  header .header-blue {
    width: 1em;
    background-color: #3b62b1;
    height: 100%; }
    @media screen and (max-width: 950px) {
      header .header-blue {
        height: 100vh; } }
  header .header-information {
    width: 100%;
    height: 100%;
    color: white;
    background-color: #313131; }
    @media screen and (max-width: 950px) {
      header .header-information {
        max-width: 18rem; } }
  header .menuContainer {
    color: white;
    margin-top: 1rem; }
  header .menuContainer h2 {
    margin: 0.8rem;
    margin-left: 1rem;
    font-weight: 500; }
  header .menuContent {
    margin-left: 1rem; }
  header .menuContentContainer {
    display: flex;
    flex-direction: row; }
  header .menuContentContainer p {
    color: white;
    flex: 1 1;
    margin: 0.5rem;
    margin-left: 1rem; }
  header .menuContent a {
    color: white;
    margin: 1rem;
    font-size: 1.5rem;
    flex: 1 1; }
  header .line {
    background-image: linear-gradient(to right, #3b62b1, transparent);
    height: 1px; }
  @media screen and (max-width: 950px) {
    header .close {
      position: absolute;
      right: 0;
      top: 0;
      width: 32px;
      height: 32px;
      padding: 15px;
      opacity: 0.3; }
    header .close:hover {
      opacity: 1; }
    header .close:before,
    header .close:after {
      position: absolute;
      left: 30px;
      content: " ";
      height: 33px;
      width: 3px;
      background-color: white; }
    header .close:before {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }
    header .close:after {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); } }
  header .eu-regolation {
    font-size: 10px;
    margin-right: 10%;
    margin-left: 10%;
    padding-bottom: 1rem; }

.only-desktop {
  display: none; }
  @media screen and (min-width: 950px) {
    .only-desktop {
      display: flex; } }

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.image-profile {
  position: absolute;
  border-radius: 50%; }

.image-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 1em; }

.image-background-animation {
  border-radius: 50%;
  border: 10px;
  background-image: linear-gradient(to right, #3b62b1, transparent);
  border-color: #313131;
  -webkit-animation: rotating 4s linear infinite;
  animation: rotating 4s linear infinite; }

footer {
  background-color: #313131;
  margin-left: 18rem;
  display: flex;
  height: auto;
  color: white; }
  footer ul {
    flex: 1 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    list-style-type: none; }
    footer ul li {
      margin: 3px;
      color: white;
      font-size: 10px;
      text-align: justify;
      margin-left: 1rem;
      margin-right: 1rem; }
      footer ul li a {
        color: white;
        font-size: 12px;
        text-decoration: none; }
    @media screen and (max-width: 950px) {
      footer ul {
        padding: 0; } }
  @media screen and (max-width: 950px) {
    footer {
      margin-left: 0px;
      padding: 0; } }

